.simplefavorite-button {
    font-family: "robotoregular", sans-serif;
    border: none;
    opacity: 1 !important;
    padding: 5px 10px;
    font-size: 13px;
    float: left;

    @include media($mobile) {
        font-size: 11px;
        padding: 4px 5px;
    }

    &:hover {
        background: #c4a020 !important;
    }

    &.active:hover {
        background: $black;
    }
}

.lessons_page {
    position: relative;

    .top_content {
        padding: 20px;

        h3 {
            font-size: 18px;
            float: left;
        }

        .simplefavorites-clear {
            font-family: "robotoregular", sans-serif;
            font-size: 14px;
            color: $red;
            border: none;
            text-decoration: underline;
            float: right;
            margin-top: 20px;
            background: none;

            @include media($mobile) {
                margin-top: 10px;
            }

            &:hover {
                color: #c30b21;
            }
        }

        button[disabled="disabled"] {
            color: $lightGray;
            text-decoration: none;

            &:hover {
                color: $lightGray;
            }
        }
    }

    #video_player {
        &.open {
            margin-bottom: 40px;
        }
    }

    #video_player,
    .course_video_player {
        background: $darkerGray;
        //max-height: 900px;
        //overflow-y: scroll;

        .content_wrap {
            position: relative;

            @include media($mobile) {
                padding: 0 25px;
            }
        }

        .lesson_title {
            text-align: center;
            color: $white;
            padding: 15px 0;
            background: rgba(0, 0, 0, 0.2);

            h3 {
                margin: 0;

                @include media($mobile) {
                    font-size: 21px;
                }
            }
        }

        .description {
            padding: 20px;
            color: $white;
            border-radius: 8px;
            background: rgba(0, 0, 0, 0.2);
            margin-top: 20px;

            ul {
                list-style: inherit;
            }

            p {
                font-size: 20px;
                line-height: 1.6;
                color: $white;

                @include media($tablet) {
                    font-size: 18px;
                }

                @include media($mobile) {
                    font-size: 16px;
                }
            }
        }

        .replace_link {
            padding: 10px 0;
            text-align: center;
            margin: 0;
            color: $white;

            @include media($smallMobile) {
                text-align: right;
            }

            a {
                font-family: "robotobold", sans-serif;
                font-size: 16px;
                color: $red;

                @include media($mobile) {
                    font-size: 14px;
                }
            }
        }

        .links_wrap {
            @include span-columns(12);
            text-align: center;
            padding-bottom: 10px;

            a {
                color: $white;
                text-transform: uppercase;
                text-decoration: underline;

                @include media($mobile) {
                    font-size: 14px;
                }

                &:hover {
                    color: $yellow;
                }

                &.keyboard_link {
                    margin-right: 0;
                }
            }
        }

        .video_iframe_wrap {
            @include span-columns(7);
            padding-left: 2.5%;
            padding-bottom: 20px;

            @include media($tablet) {
                @include span-columns(8);
                padding-right: 2.5%;
                padding-bottom: 0;
            }

            iframe {
                border-radius: 8px;
            }

            .top_row {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-orient: horizontal;
                -webkit-box-direction: normal;
                -ms-flex-direction: row;
                flex-direction: row;
                -ms-flex-wrap: nowrap;
                flex-wrap: nowrap;
                justify-content: space-between;
                -webkit-justify-content: space-between;
                align-items: center;
                -webkit-align-items: center;
                padding: 20px 0;

                .button_wrap {
                    width: 50%;
                }

                .links_wrap {
                    width: 50%;
                    text-align: right;
                }
            }
        }

        .video_content_wrap {
            @include span-columns(5);
            padding-right: 2.5%;
            max-height: 800px;
            overflow-y: scroll;
            margin-top: 15px;

            @include media($tablet) {
                @include span-columns(8);
                padding-left: 2.5%;
            }
        }
    }

    .video_list {
        margin: 40px 0;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        box-shadow: 0px 0px 14px -5px rgba(0, 0, 0, 0.4);

        @include media($mobile) {
            margin: 20px 0;
        }

        .column {
            width: 32.9%;
            display: inline-block;
            //float: left;
            padding: 20px;

            @include media($tablet) {
                padding: 15px;
            }

            @include media($mobile) {
                width: 49.5%;
                padding: 10px;
            }

            .vid_image_wrap {
                position: relative;

                img {
                    border-radius: 8px;
                }

                .simplefavorite-button {
                    position: absolute !important;
                    right: 0;
                    bottom: 0;
                    border-top-right-radius: 0;
                    border-bottom-left-radius: 0;
                    border-bottom-right-radius: 7px;

                    /* @include media($mobile) {
                        position: relative !important;
                        right: auto;
                        bottom: auto;
                        width: 100%;
                    } */
                }
            }

            a {
                display: block;

                &:not(.button) {
                    box-shadow: 0px 0px 14px -5px rgba(0, 0, 0, 0.4);

                    &:hover:before {
                        content: "";
                        text-align: center;
                        background: url("../images/play-video.png") no-repeat;
                        background-size: 12%;
                        background-position: center center;
                        background-color: rgba(0, 0, 0, 0.6);
                        position: absolute;
                        height: 100%;
                        width: 100%;
                        top: 0;
                        left: 0;
                        border-radius: 8px;
                    }
                }
            }

            .comment_wrap {
                display: none;
            }

            h4 {
                font-family: "robotoregular", sans-serif;
                margin: 4px 0;
                font-size: 14px;
                line-height: 1.2;

                @include media($smallMobile) {
                    font-size: 12px;
                }
            }

            p {
                line-height: 1.2;
                font-size: 14px;
                margin: 0;
                color: $grayText;

                @include media($smallMobile) {
                    font-size: 12px;
                }
            }
        }
    }

    .filter_controls {
        @include span-columns(12);
        padding: 20px;
        background: $darkerGray;
        color: $white;
        margin-bottom: 20px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;

        .search_box {
            @include span-columns(12);
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            margin-top: auto;
            margin-bottom: auto;

            input {
                width: 100%;
                border: none;
                height: 45px;
                padding: 0 10px;
                font-size: 16px;
                -webkit-border-radius: 4px;
                -moz-border-radius: 4px;
                border-radius: 4px;
                background: #fff url(../images/icon-search.png) no-repeat;
                background-size: 22px;
                background-position: 98% 50%;
                color: $darkGray;

                @include media($mobile) {
                    height: 40px;
                    background-size: 18px;
                }
            }
        }

        .filters {
            @include span-columns(12);
            position: relative;
            text-align: center;
            padding-left: 22px;
            z-index: 1;
            margin-bottom: 20px;

            @include media($tablet) {
                padding-top: 10px;
                padding-left: 0;
            }

            p {
                font-size: 10px;
                margin: 0;
            }

            h3 {
                font-size: 18px;
                margin: 0;
                text-transform: none;

                @include media($tablet) {
                    font-size: 16px;
                }

                span {
                    padding-left: 2px;
                }
            }
        }

        .filter_list {
            margin: 0;

            li {
                display: inline-block;
                margin-right: 2%;
                font-size: 14px;
                cursor: pointer;
                color: $lightGray;
                border-bottom: 1px solid $lightGray;
                padding-bottom: 2px;
                padding-top: 5px;

                @include media($tablet) {
                    font-size: 13px;
                }

                &:hover {
                    color: $yellow;
                    border-bottom: 1px solid $yellow;

                    @include media($tablet) {
                        color: $lightGray;
                        border-bottom: 1px solid $lightGray;
                    }
                }

                &.active {
                    color: $yellow;
                    border-bottom: 1px solid $yellow;
                }
            }
        }
    }
    .filtr-container {
        overflow: hidden;
        //height: auto !important;

        &:before,
        &:after {
            display: table;
            content: "";
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
        }

        .text_wrap {
            text-align: center;
            margin: 10% 0 15%;

            @include media($mobile) {
                margin: 5% 0 15%;
            }

            h2 {
                width: 100%;
                float: left;
                border: none;
                text-transform: uppercase;
            }

            .button {
                padding: 10px 50px 10px 20px;
                position: relative;

                &:before {
                    content: "";
                    position: absolute;
                    background: url(../images/white-arrow.png) no-repeat;
                    width: 28px;
                    height: 12px;
                    right: 12px;
                    z-index: 1;
                    top: 16px;
                    background-size: 100%;

                    @include media($mobile) {
                        top: 14px;
                        width: 25px;
                        right: 13px;
                    }
                }
            }
        }
    }
}

#comments {
    @include span-columns(12);
    position: relative;
    padding: 30px 0;
    text-align: left;

    .comments-title {
        display: none;
    }

    ol {
        @include span-columns(12);
        list-style: none;
        margin: 0;

        li {
            @include span-columns(12);
            margin-bottom: 20px;

            .children {
                @include span-columns(12);
                padding-left: 6.7%;
                margin-top: 5px;

                li {
                    margin-bottom: 5px;
                }
            }
        }
    }

    .comment-meta {
        @include span-columns(1);
        background: $white;
        padding: 5px;
        text-align: center;
        margin-right: 0.5%;

        &.mobile {
            display: none;

            @include media($mobile) {
                @include span-columns(1);
                background: transparent;
                margin-right: 5px;
                max-width: 60px;
            }
        }

        &.desktop {
            @include media($mobile) {
                display: none;
            }
        }
    }

    .comment-content {
        @include span-columns(11.2);
        position: relative;
        background: $white;
        border: 1px solid $darkGray;
        text-align: center;

        @include media($mobile) {
            @include span-columns(4);
        }

        .comment_heading {
            padding: 20px 20px 10px 20px;
            background: $lightGray;
            color: $red;
            text-align: left;

            @include media($mobile) {
                padding: 5px 10px 5px 5px;
            }
        }

        > p {
            @include span-columns(12);
            padding: 10px 20px;
            text-align: left;
        }

        .video_embed {
            width: 50%;
            display: inline-block;
            margin: 20px auto 0;
            padding: 0 20px;

            @include media($tablet) {
                width: 75%;
            }

            @include media($mobile) {
                width: 100%;
            }
        }

        .comment-metadata {
            width: 100%;
            float: left;
            font-size: 12px;
            line-height: 1.2;

            @include media($mobile) {
                @include span-columns(3);
                padding-top: 20px;
                font-size: 10px;
            }

            a {
                float: left;
                width: 100%;
                color: $red;
            }

            .fn {
                font-family: "robotobold", sans-serif;
                float: left;
                line-height: 1.1;
                font-size: 16px;

                @include media($mobile) {
                    font-size: 14px;
                }
            }

            .says {
                float: left;
                font-size: 16px;
                margin-left: 6px;

                @include media($mobile) {
                    font-size: 14px;
                }
            }
        }
    }

    .reply {
        @include span-columns(12);
        float: right;
        padding: 20px;
        text-align: right;
        margin-right: 0;

        .reply_button {
            display: inline-block;

            a {
                font-family: "robotoregular", sans-serif;
                font-size: 16px;
                padding: 7px 15px;
                border-radius: 4px;
                -webkit-border-radius: 4px;
                -moz-border-radius: 4px;
                background: $red;
                color: $white;

                &:hover {
                    background: #c30b21;
                }

                @include media($tablet) {
                    padding: 6px 12px;
                    font-size: 14px;
                }

                @include media($mobile) {
                    padding: 7px 10px;
                    font-size: 12px;
                }

                @include media($smallMobile) {
                    padding: 5px 8px;
                }
            }
        }

        .button_wrap {
            float: right;
            margin-left: 10px;
        }
    }

    #respond {
        @include span-columns(12);

        .logged-in-as {
            display: none;
        }
    }

    .comment_submit {
        input {
            font-family: "robotoregular", sans-serif;
            float: left;
            background: $red;
            color: $white;
            font-size: 16px;
            padding: 7px 15px;
            border-radius: 4px;
            -webkit-border-radius: 4px;
            -moz-border-radius: 4px;
            border: none;

            &:hover {
                background: #c30b21;
            }

            @include media($tablet) {
                padding: 6px 12px;
                font-size: 14px;
            }

            @include media($mobile) {
                padding: 7px 10px;
                font-size: 12px;
            }

            @include media($smallMobile) {
                padding: 5px 8px;
            }
        }

        .loading_gif {
            float: left;
            width: 20px;
            height: 20px;
            margin-left: 5px;
            margin-top: 8px;
            text-align: center;

            @include media($mobile) {
                width: 15px;
                height: 15px;
                margin-top: 6px;
            }
        }
    }
}

.comment_reply_wrap {
    width: 100%;
    float: left;
    display: none;
    margin-top: 20px;
    position: relative;

    .cancel_comment {
        display: inline-block;
        position: absolute;
        bottom: 0;
        right: 0;
    }

    #reply-title {
        color: $black;
    }
}

.comment-respond,
.comment_reply_wrap {
    h3 {
        margin-top: 0;
        font-size: 20px !important;
        text-align: left;

        @include media($tablet) {
            font-size: 18px !important;
        }

        @include media($mobile) {
            font-size: 16px !important;
        }
    }

    form {
        @include span-columns(12);
        text-align: left;

        .comment-form-comment {
            @include span-columns(12);

            label {
                display: none;
            }

            textarea {
                @include span-columns(12);
                height: 100px;
            }
        }
        .comment_submit {
            float: left;
        }
    }
}

.comment-awaiting-moderation {
    font-family: "robotobold", sans-serif !important;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    z-index: 1;
    text-align: center;
    background: rgba(0, 0, 0, 0.6);
    font-size: 24px !important;
    color: $yellow;
    line-height: 1.4;
    padding-top: 5% !important;

    @include media($tablet) {
        padding-top: 10% !important;
    }

    @include media($mobile) {
        padding-top: 15% !important;
    }
}

.keyboard_popup {
    display: none;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background: rgba(0, 0, 0, 0.7);
    top: 0;
    min-height: 100%;
    position: fixed;
    padding: 5%;
    text-align: center;

    .iframe_wrap {
        width: 75%;
        height: 100%;
        margin: 0 auto;
        position: relative;

        @include media($tablet) {
            width: 100%;
        }

        .close_button {
            position: absolute;
            right: -14px;
            top: -14px;
            z-index: 999;
        }

        iframe {
            height: 100%;
            width: 100%;
        }
    }
}

.video_content_wrap #comments {
    padding-bottom: 20px;
    color: #454647;

    .comment-content {
        width: 100%;

        .comment_heading {
            padding: 10px;
        }
    }

    .comment-reply-title {
        color: $white;
    }

    .comment_reply_wrap {
        #reply-title {
            color: $black;
        }
    }

    .comment-metadata {
        @include span-columns(10);
        padding-top: 10px;
        font-size: 10px;
    }

    .comment-meta {
        @include span-columns(2);
        background: transparent;
        margin-right: 5px;
        max-width: 60px;

        &.desktop {
            display: none;
        }

        &.mobile {
            display: block;
        }
    }

    .comment-reply-title span {
        font-size: 14px;
        margin-top: 5px;

        @include media($mobile) {
            font-size: 12px;
        }
    }
}

#respond #comment-form-identity {
    display: none !important;
}

#commentform iframe {
    height: 230px !important;
}

.lessons_page.courses {
    .course_video_player {
        margin-top: 20px;
        position: relative;

        @include media($mobile) {
            margin-top: 40px;
        }

        &.open {
            &:before {
                content: "";
                position: absolute;
                width: 0;
                height: 0;
                border-left: 30px solid transparent;
                border-right: 30px solid transparent;
                border-bottom: 30px solid;
                border-bottom-color: #373839;
                top: -22px;
                left: 50%;
                margin-left: -20px;
                z-index: -1;
            }
        }
    }

    .row {
        padding: 40px 0;
        border-bottom: 1px solid $darkGray;

        &:first-child {
            padding-top: 0;
        }

        &.open_player {
            padding-bottom: 0;
            margin-bottom: 0;
        }

        .column {
            @include span-columns(6);
            text-align: left;

            @include media($mobile) {
                @include span-columns(4);
                padding: 0;
            }

            &:last-child {
                padding-left: 40px;

                @include media($mobile) {
                    padding-left: 0;
                }
            }

            a {
                text-transform: uppercase;
                display: inline-block;
            }

            h1 {
                margin-top: 0;
                font-size: 28px;
                margin-bottom: 5px;
                line-height: 1.2;

                @include media($tablet) {
                    font-size: 26px;
                }

                @include media($mobile) {
                    margin-top: 10px;
                }

                a {
                    color: $black;
                }
            }

            h4 {
                margin: 5px 0;
                font-size: 12px;

                a {
                    color: $black;
                    text-decoration: underline;
                }

                &.sub_title {
                    font-size: 18px;
                }
            }

            p {
                @include media($tablet) {
                    font-size: 14px;
                }
            }

            .button_wrap {
                margin-top: 20px;
                float: left;

                @include media($smallMobile) {
                    @include span-columns(4);
                }

                .button {
                    padding: 10px 30px;

                    @include media($smallMobile) {
                        width: 100%;
                        display: block;
                        text-align: center;
                    }
                }
            }
        }

        &:last-child {
            border-bottom: 1px solid $darkGray;
        }
    }
}

.video_container {
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
}

.ld-course-list-content {
    padding: 80px 0;

    @include media($tablet) {
        padding: 60px 0;
    }

    @include media($mobile) {
        padding: 40px 0;
    }

    .entry-title {
        font-size: 22px;

        @include media($tablet) {
            font-size: 20px;
        }
    }

    .ld-course-list-items .ld_course_grid {
        @include media($mobile) {
            margin: 0 auto 20px;
        }

        .btn-primary {
            background-color: $red;
            border-color: $red;

            &:hover {
                background-color: $darkRed;
            }
        }

        .thumbnail.course {
            height: 100%;

            .ld_course_grid_price.ribbon-enrolled {
                background: $red;
            }
        }
    }
}

.learndash-wrapper {
    input[type="button"],
    .ld-button,
    input[type="submit"] {
        @include robotoreg;
        border-radius: 4px !important;
        -webkit-border-radius: 4px !important;
        -moz-border-radius: 4px !important;
        font-size: 16px !important;
        padding: 7px 15px !important;

        &:hover {
            background-color: $darkRed !important;
        }

        @include media($mobile) {
            font-size: 14px !important;
            width: 100% !important;
            max-width: unset !important;
        }
    }

    .wpProQuiz_content .wpProQuiz_button2 {
        @include media($mobile) {
            margin-bottom: 1em !important;
        }
    }

    .ld-item-list .ld-item-list-item {
        border: 2px solid $red;

        .ld-item-list-item-preview .ld-status-icon {
            margin-top: 0;

            .ld-icon:before {
                transform: translateY(-70%) translateX(-50%);
            }
        }

        .ld-item-list-item-preview {
            padding: 0;

            &:hover {
                background-color: $lightGray;
            }

            a {
                padding: 20px;

                &:hover {
                    background-color: $lightGray;
                }
            }
        }
    }

    .wpProQuiz_content {
        .wpProQuiz_questionListItem label {
            &:focus-within {
                border-color: $darkRed;
            }

            &.is-selected {
                border-color: $red;
            }
        }

        .wpProQuiz_question_text {
            margin-top: 20px;

            p {
                @include robotobold;
                font-size: 24px;

                @include media($tablet) {
                    font-size: 20px;
                }

                @include media($mobile) {
                    font-size: 18px;
                }
            }
        }
    }
}

.metro_wrap {
    width: 500px;
    height: 600px;
}

.banner_wrap {
    margin: 20px 0 20px;
    padding: 0px 20px;
    border-radius: 8px;
    box-shadow: 0px 0px 14px -5px rgba(0, 0, 0, 0.4);

    .title_wrap {
        @include span-columns(12);
        padding: 10px;

        h3 {
            @include robotobold;
            margin: 0;
            text-align: center;
            width: 100%;
            font-size: 2.2rem;
        }
    }

    ul {
        width: 100%;
        margin: 0;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;

        @include media($tablet) {
            grid-template-columns: repeat(3, 1fr);
        }

        @include media($mobile) {
            grid-template-columns: repeat(2, 1fr);
        }

        li {
            &:last-child {
                @include media($tablet) {
                    display: none;
                }
            }

            &:nth-child(3) {
                @include media($mobile) {
                    display: none;
                }
            }

            a {
                color: $black;
            }

            a.image_link {
                position: relative;
                display: block;
                width: 100%;

                img {
                    border-radius: 8px;
                }

                p {
                    @include robotoreg;
                    color: $black;
                    position: absolute;
                    bottom: -33px;
                    left: 1px;
                    font-size: 1.5rem;
                }

                &:hover::before {
                    content: "";
                    background: url("../images/go-arrow.png") no-repeat;
                    background-position: center center;
                    background-size: 10%;
                    background-color: rgba(0, 0, 0, 0.8);
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    border-radius: 8px;
                    z-index: 1;
                }
            }
        }
    }
}
