.join_text {
    font-family: "robotobold", sans-serif;
    text-align: center;
    color: $black;
    margin-top: 40px;

    a {
        text-transform: uppercase;
        color: $red;
    }
}
.pmpro_login_wrap,
.pmpro_lost_password_wrap {
    text-align: center;
    position: relative;

    hr {
        display: none;
    }

    &.pmpro_card {
        border: none;
        box-shadow: none;
        margin: 0;
    }
    .pmpro_card_content {
        padding-top: 0 !important;
        padding-bottom: 20px;
    }
    .pmpro_card_actions {
        background-color: unset;
        border: none !important;
        padding: 0;
    }

    .pmpro_actions_nav {
        a {
            @include robotomed;
            color: $red;

            @include media($mobile) {
                font-size: 12px;
            }

            &:hover {
                color: $darkRed;
            }
        }
    }
}

.pmpro_lost_password_wrap .pmpro_actions_nav {
    display: none;
}
.pmpro_section {
    #loginform {
        .login-submit {
            .button:not(#pmpro_btn-password-toggle-1) {
                font-family: "robotoregular", sans-serif;
                background: $black;
                padding: 8px 24px 12px;
                color: $white;
                font-size: 18px;
                border-radius: 4px;
                -webkit-border-radius: 4px;
                -moz-border-radius: 4px;
                height: 40px;
                border: none;

                @include media($tablet) {
                    font-size: 16px;
                }

                @include media($mobile) {
                    font-size: 14px;
                    height: 35px;
                    padding: 0;
                }

                &:hover {
                    background: #4d4d54;
                }
            }
        }
    }
}

#loginform,
#lostpasswordform,
#red_registration_form {
    float: none;
    @include robotoreg;
    font-size: 15px;
    margin: 100px auto;
    text-align: left;
    background: $lightGray;
    padding: 40px 40px 60px 40px;
    -webkit-box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    -moz-box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    border: 1px solid $borderGray;
    color: $black;
    max-width: 500px;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;

    @include media($tablet) {
        width: 425px;
    }

    @include media($mobile) {
        width: 375px;
        padding: 25px;
        font-size: 14px;
    }

    @include media($smallMobile) {
        width: 100%;
    }

    input[type="submit"],
    button:not(#pmpro_btn-password-toggle-1) {
        font-family: "robotoregular", sans-serif;
        background: $black;
        padding: 8px 24px 12px;
        color: $white;
        font-size: 18px;
        border-radius: 4px;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;
        height: 40px;
        border: none;

        @include media($tablet) {
            font-size: 16px;
        }

        @include media($mobile) {
            font-size: 14px;
            height: 35px;
            padding: 0;
        }

        &:hover {
            background: #4d4d54;
        }
    }

    legend {
        display: none;
    }

    fieldset {
        border: none;
    }

    label {
        @include robotolight;
        color: $black;
        line-height: 22px;
        margin-bottom: 8px;
    }

    input {
        height: 40px;
        border: 1px solid $borderGray;
        padding: 10px;
        max-width: unset !important;
        width: 100%;

        @include media($mobile) {
            height: 35px;
            width: 100% !important;
        }
    }

    .login-remember {
        input {
            height: 16px;
            width: 20px;
        }
    }

    .som-password-sent-message {
        text-align: center;
        color: $black;
    }
}

#lostpasswordform {
    margin-top: 20px;
    margin-bottom: 20px;
}

#password-lost-form-wrap a {
    color: $red;
    font-size: 18px;
    width: 100%;
    float: left;
}

#loginform {
    margin: 30px auto 100px;
}

.confirmation_page {
    margin: 100px 0;
    background: $lightGray;
    padding: 40px 40px 80px;
    text-align: center;
    -webkit-box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    -moz-box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    border: 1px solid $borderGray;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;

    @include media($tablet) {
        padding: 30px 30px 70px;
    }

    @include media($mobile) {
        margin: 40px 0;
        padding: 20px 20px 40px;
    }

    .heading {
        font-size: 20px;
        margin-bottom: 20px;

        @include media($mobile) {
            font-size: 18px;
        }

        span {
            color: $red;
        }
    }

    .button_wrap {
        margin-top: 20px;
    }

    ul {
        margin-top: 60px;
        display: inline-block;

        @include media($mobile) {
            margin-top: 40px;
        }

        li {
            @include span-columns(12);
            float: none;
            background: $white;
            padding: 12px 30px;
            margin-bottom: 20px;

            @include media($mobile) {
                font-size: 14px;
                width: 100%;
            }

            p {
                margin: 0;
                display: inline-block;
            }

            span {
                display: inline-block;
                color: $red;

                @include media($mobile) {
                    width: 100%;
                }
            }
        }
    }
}

#pmpro_levels_table {
    width: 100%;
    float: left;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    color: $black;
    text-align: center;
    -webkit-box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    -moz-box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    box-shadow: -2px 2px 3px 1px rgba(0, 0, 0, 0.14);
    border-top: 1px solid $borderGray;
    border-left: 1px solid $borderGray;
    border-bottom: 1px solid $borderGray;

    @include media($tablet) {
        display: block;
        border: none;
    }

    @include media($mobile) {
        margin: 50px auto;
    }

    .column {
        width: 25%;
        float: left;
        border-right: 1px solid $borderGray;
        background: $lightGray;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;

        @include media($tablet) {
            width: 50%;
            display: block;
            border-top: 1px solid $borderGray;
            border-bottom: 1px solid $borderGray;
        }

        @include media($smallMobile) {
            width: 100%;
        }

        &:nth-child(3) {
            @include media($tablet) {
                margin-top: 60px;
                padding-bottom: 3px;
            }

            @include media($smallMobile) {
                margin-top: 0;
                padding-bottom: 0;
            }
        }

        &:last-child {
            border-right: 4px solid #f2d12d;
            border-left: 4px solid #f2d12d;
            border-bottom: 4px solid #f2d12d;
            margin-top: -44px;

            @include media($tablet) {
                margin-top: 16px;
            }

            @include media($smallMobile) {
                margin-top: 0;
            }

            .column_heading {
                background: $black;
            }
        }
    }

    .highlight {
        width: 100%;
        text-align: center;
        background: #f2d12d;
        padding: 10px 0;

        img {
            width: auto;
            display: inline-block;
            vertical-align: bottom;
            margin-right: 5px;
        }

        p {
            font-family: "robotomedium", sans-serif;
            display: inline-block;
            margin: 0;
            font-size: 18px;
            vertical-align: top;
        }
    }

    .column_heading {
        background: $red;
        color: $white;
        padding: 15px 5px 10px;

        h2 {
            font-family: "robotobold", sans-serif;
            font-size: 27px;
            margin-top: 0;
        }

        p {
            font-family: "robotoregular", sans-serif;
            font-size: 14px;
            margin: 0;
        }
    }

    .cost {
        background: $white;
        padding: 0 10px 20px;

        h3 {
            font-family: "robotobold", sans-serif;
            display: inline-block;
            font-size: 30px;
            margin: 20px 0;
        }

        p {
            font-family: "robotoregular", sans-serif;
            font-size: 14px;
            color: $grayText;
            margin-bottom: 0;
        }

        .button_wrap {
            margin-top: 20px;

            a {
                display: inline-block !important;
                font-size: 16px;
                padding: 10px 20%;
            }
        }
    }

    .description {
        padding: 20px 10px;
        background: $lightGray;

        ul {
            margin: 0;
            text-align: left;
            color: $black;
            background: url("../images/bass-head.png") no-repeat center center;
            background-size: 50%;

            @include media($tablet) {
                display: inline-block;
                background-size: 60%;
            }

            li {
                position: relative;
                padding-left: 50px;
                font-size: 14px;

                &:before {
                    content: "";
                    position: absolute;
                    background: url("../images/check.png") no-repeat;
                    width: 20px;
                    height: 15px;
                    left: 21px;
                    top: 5px;
                }
            }
        }
    }
}
