// typography baselines and @font face setups

@font-face {
    font-family: 'calvertmtbold2';
    src: url('fonts/calvertmt-bold2-webfont.ttf') format('truetype'),url('fonts/calvertmt-bold2-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'poiretoneregular';
    src: url('fonts/PoiretOne-Regular.ttf') format('truetype'),url('fonts/poiretone-regular-webfont.woff2') format('woff2'),url('fonts/poiretone-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'robotoblack';
    src: url('fonts/Roboto-Black.ttf') format('truetype'),url('fonts/roboto-black-webfont.woff2') format('woff2'),url('fonts/roboto-black-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'robotoblackitalic';
    src: url('fonts/Roboto-BlackItalic.ttf') format('truetype'),url('fonts/roboto-blackitalic-webfont.woff2') format('woff2'),url('fonts/roboto-blackitalic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'robotobold';
    src: url('fonts/Roboto-Bold.ttf') format('truetype'),url('fonts/roboto-bold-webfont.woff2') format('woff2'),url('fonts/roboto-bold-webfont.woff') format('woff');
