    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'robotobolditalic';
    src: url('fonts/Roboto-BoldItalic.ttf') format('truetype'),url('fonts/roboto-bolditalic-webfont.woff2') format('woff2'),url('fonts/roboto-bolditalic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'robotoitalic';
    src: url('fonts/Roboto-Italic.ttf') format('truetype'),url('fonts/roboto-italic-webfont.woff2') format('woff2'),url('fonts/roboto-italic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'robotolight';
    src: url('fonts/Roboto-Light.ttf') format('truetype'),url('fonts/roboto-light-webfont.woff2') format('woff2'),url('fonts/roboto-light-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'robotolightitalic';
    src: url('fonts/Roboto-LightItalic.ttf') format('truetype'),url('fonts/roboto-lightitalic-webfont.woff2') format('woff2'),url('fonts/roboto-lightitalic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'robotomedium';
    src: url('fonts/Roboto-Medium.ttf') format('truetype'),url('fonts/roboto-medium-webfont.woff2') format('woff2'),url('fonts/roboto-medium-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'robotomediumitalic';
    src: url('fonts/Roboto-MediumItalic.ttf') format('truetype'),url('fonts/roboto-mediumitalic-webfont.woff2') format('woff2'),url('fonts/roboto-mediumitalic-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'robotoregular';
    src: url('fonts/Roboto-Regular.ttf') format('truetype'),url('fonts/roboto-regular-webfont.woff2') format('woff2'),url('fonts/roboto-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

h1, h2 {
    @include robotobold;
}

h3, p {
    @include robotolight;
}
